.navbar{
    position: fixed;
    top: 0;
    justify-content: space-between;
    display: flex;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    
}
.nav_black{
    background-color: #111;

}
.logo{
    object-fit: contain;
    width: 80px;
    position: fixed;
    left: 20px;

}

.avatar{
   object-fit: contain;
   width: 30px;
   position: fixed;
   right: 20px;;

}