.banner {
    height: 449px;
    color: white;
    object-fit: contain;
    
}
.banner_contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    height: 80px;
    max-width: 360px;

}
.banner_button {
    color: #fff;
    outline:none;
    border:none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
}
.banner_button:hover {
    color: black;
    background-color: #e6e6e6;
    transition: all 0.2s;

}
.banner--fadeBottom {
    height:  7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
}